import service from "../../service.js";
export default{
	data(){
		return{
			original:'',
			pwd2:'',
			repwd2:''
		}
	},
	methods:{
		jump(url){
			uni.navigateTo({
				url:url
			})
		},
		btns(){
			/* service.auth(this , service.api.set.changePwd2 , 'POST' , {
				original: this.original,
				pwd2: this.pwd2,
				repwd2: this.repwd2
			} , function(self , res){
				console.log(res)
				uni.showToast({
					icon:'none',
					title:res.info
				});
				setTimeout(()=>{
				uni.switchTab({
					url:"/pages/main/my"
				})
				},1500);
			}); */
			
			
			uni.request({
				url:service.api.set.changePwd2,
				method:"POST",
				data:{
				original: this.original,
				pwd2: this.pwd2,
				repwd2: this.repwd2
			},
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('token')
				},
				success: (res) => {
					var res = res.data;
					if(res.code == 1){
						uni.showToast({
							icon:'none',
							title:res.info
						});
						setTimeout(()=>{
						uni.switchTab({
							url:"/pages/main/my"
						})
						},1500);
					}else{
						uni.showToast({
							icon:'none',
							title:res.info
						});
					}
				},
				fail: (res) => {
					var res = res.data;
					uni.showToast({
						icon:'none',
						title:res.info
					});
				}
			});
		}
	}
	
}
